.App {
  text-align: center;
}

.selection-header {

}

.selection-header-selected {
  text-decoration: underline;
}

.selection-header:hover {
  cursor: pointer;
}

.note-select {
  border: 1px solid black;
  width: 100%;
  border-radius: 4px;
  font-size: 18px;
  padding: 10px;
  margin-top: 6px;
}

.note-select:hover {
  cursor: pointer;
}

.note-select-selected {
  border: 3px solid black;
  background-color: lavender;
  margin-top: 4px;
}

.displayed-note {
  /* display: inline; */
  /* justify-content: space-evenly; */
  min-width: 120px;
}

.displayed-notes-container {
  margin-top: 20px;
  width: 100%;
  border: 2px solid black;
  border-radius: 10px;
  font-size: 18px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

